import React, { Component } from "react";
import { makeStyles } from '@material-ui/core/styles';


export default function NavBar(){
    const useStyles = makeStyles((theme) => ({
        ul: {listStyleType: "none", 
                        margin: "0",
                        padding: "0",
                        overflow: "hidden",
                        backgroundColor:"#333",
                        position: "sticky", 
                        width: "100%"},
        li :{float: "left",
            fontSize:"1.2em"},

        liright : {float: "right",
        fontSize:"1.2em"},
        a :{
                display: "block",
                color: "white",
                textAlign: "center",
                padding: "14px 16px",
                textDecoration: "none",
                fontFamily: [
                    '-apple-system',
                    'BlinkMacSystemFont',
                    '"Segoe UI"',
                    'Roboto',
                    'Oxygen',
                    'Ubuntu',
                    'Cantarell', 
                    'Fira Sans', 
                    'Droid Sans',
                    'Helvetica Neue',
                    'sans-serif',
                  ],
                  "&:hover": {
                    backgroundColor: "#111",
                  },
              }
      }));
      const classes = useStyles();
      let role = localStorage.getItem("role");
      if(role != "E1"){
        return(
            <div className="bar">
                <ul className={classes.ul}>
                    <li className={classes.li}><a className={classes.a} href="home">Home</a></li>
                    <li className={classes.li}><a className={classes.a} href="askquizz">Quizz</a></li>
                    <li className={classes.li}><a className={classes.a} href="trail">Trail</a></li>
                    <li className={classes.liright}><a className={classes.a} href="logout">Logout</a></li>
                    <li className={classes.liright}><a className={classes.a} href="settings">Settings</a></li>
                    <li className={classes.liright}><a className={classes.a} href="backoffice">Backoffice</a></li>
                </ul>
            </div>
            );
      }
      else {
        return(
            <div className="bar">
                <ul className={classes.ul}>
                    <li className={classes.li}><a className={classes.a} href="home">Home</a></li>
                    <li className={classes.li}><a className={classes.a} href="askquizz">Quizz</a></li>
                    <li className={classes.li}><a className={classes.a} href="trail">Trail</a></li>
                    <li className={classes.liright}><a className={classes.a} href="logout">Logout</a></li>
                    <li className={classes.liright}><a className={classes.a} href="settings">Settings</a></li>
                </ul>
            </div>
            );
      }
    }