const GreenCheckbox = withStyles({
    root: {
      color: green[400],
      '&$checked': {
        color: green[600],
      },
    },
    checked: {},
  })((props) => <Checkbox color="default" {...props} />);

export default class TrailResume extends Component{
    
    constructor(props){
        super(props);
        this.state = {
            name:props.trail.name,
            description: props.trail.description,
            trailImg:props.trail.trailImg,
            creator:props.trail.creator,
            start:props.trail.start,
            end:props.trail.end,
            markers:props.trail.markers,
            avgRating:props.trail.avgRating,
            nRatings:props.trail.nRatings,
            dist:props.trail.dist,
            verified:props.trail.verified,
        };
    }



    render(){
        return(
            <div>
            <Grid container>
                <Grid item xs={12}>
                    <h5 className= "Enunciated">Trail Name: </h5>
                </Grid>
                <Grid item xs={12}>
                    <a className= "Text">{this.state.name}</a>
                </Grid>
                <Grid item xs={12}>
                    <img src={this.state.trailImg} alt="" className="img-responsive"/>
                </Grid>
                <Grid item xs={12}>
                    <Rating name="read-only" value={this.state.avgRating} size= "large" readOnly />
                </Grid>
                <Grid item xs={12}>
                    <h5 className= "Enunciated">Description: </h5>
                </Grid>
                <Grid item xs={12}>
                    <a className= "Text">{this.state.description}</a>
                </Grid>
                <Grid item xs={6}>
                    <a className= "Enunciated">Created By: </a><a className= "Text">{this.state.creator}</a>
                </Grid>
                <Grid item xs={6}>
                    <a className= "Enunciated">Total Distance: </a><a className= "Text">{this.state.dist}KM</a>
                </Grid>
                <Grid item xs={6}>
                <FormControlLabel
                    control={<GreenCheckbox checked={this.state.verified} disable="true" name="checkedG" />}
                    label="Verified"
                    labelPlacement="start"
                    />
                </Grid>

            </Grid>
            </div>
        );
    }
}
